/* eslint-disable camelcase */
/* eslint-disable prefer-promise-reject-errors */
import React, { useState } from 'react';
import { connect } from 'react-redux';
import {
  Button,
  CustomInput,
  FormGroup,
  Input,
  InputGroup,
  Label,
  ModalBody,
  ModalFooter,
  ModalHeader,
} from 'reactstrap';
import IntlMessages from '../../helpers/IntlMessages';
import { wait } from '../../helpers/Utils';
import useRequest from '../../hooks/use-request';
import { getSponsors } from '../../redux/actions';
import { NotificationManager } from '../common/react-notifications';

const AddSponsorModel = ({
  setOpenAddModal,
  id,
  accessToken,
  getSponsorsAction,
}) => {
  const formData = new FormData();
  const [values, setValues] = useState({
    hackathon_id: id,
    name: '',
    logo: null,
  });
  
  const handleFileChange = (event) => {
    setValues({ ...values, [event.target.id]: event.target.files[0] });
    formData.delete(event.target.id);
    formData.append(`${event.target.id}`, values[event.target.id]);

  };

  const handleChange = (event, key, value = false) => {
    setValues({
      ...values,
      [key]: value ? event : event.target.value,
    });
  };

  const [doRequest, loading] = useRequest({
    url: '/addSponsors',
    method: 'POST',
    headers: {
      'Content-Type': 'multipart/form-data',
      Authorization: `Bearer ${accessToken}`,                
    },
    data: formData,
    nextSuccess: () => {
      NotificationManager.success(
        'Sponsor added successfully',
        'Application Update Success',
        3000,
        null,
        null,
        ''
      );
      getSponsorsAction(accessToken, id);
      wait(1200, () => {
        setValues({ ...values, password: 'null' });
        setOpenAddModal(false);
      });
    },
    nextError: (error) => {
      NotificationManager.error(
        error,
        'Application Update Error',
        3000,
        null,
        null,
        ''
      );
    },
  });

  const handleSubmit = () => {
    formData.append('hackathon_id', id);
    formData.append('name', values.name);
    formData.append('logo',values.logo);
    doRequest();
  };

  return (
    <>
      <ModalHeader>
        <h2 className="lead">
          <IntlMessages id="challenges.modal.application-add-sponsor-title" />
        </h2>
      </ModalHeader>

      <ModalBody>
        <FormGroup>
          <Label>
            <IntlMessages id="challenges.modal.sponsor" />
          </Label>
          <Input
            type="text"
            value={values.name}
            onChange={(event) => handleChange(event, 'name')}
          />
        </FormGroup>

        <FormGroup>
          <Label>
            <IntlMessages id="challenges.modal.sponsor-upload-image" />
          </Label>
          <InputGroup>
            <CustomInput 
              type="file"
              id="logo" 
              name="customFile"
              accept="image/jpeg, image/jpg, image/png"
              onChange={handleFileChange}
            />
          </InputGroup>
        </FormGroup>
      </ModalBody>

      <ModalFooter>
        <Button
          id="add-sponsor-button"
          color="primary"
          className={`btn-shadow btn-multiple-state ${
            loading ? 'show-spinner' : ''
          }`}
          onClick={() => handleSubmit(values)}
        >
          <span className="spinner d-inline-block">
            <span className="bounce1" />
            <span className="bounce2" />
            <span className="bounce3" />
          </span>
          <span className="label">
            <IntlMessages id="challenges.modal.application-add" />
          </span>
        </Button>
        <Button color="danger" onClick={() => setOpenAddModal(false)}>
          <IntlMessages id="challenges.modal.application-cancel" />
        </Button>
      </ModalFooter>
    </>
  );
};

const mapStateToProps = ({ authUser, applicationData }) => {
  const { accessToken } = authUser;
  const { sponsors, loading, error } = applicationData;
  return { accessToken, sponsors, loading, error };
};

export default connect(mapStateToProps, {
  getSponsorsAction: getSponsors,
})(AddSponsorModel);
