import React, { useState } from 'react';
import {
  Button,
  Card,
  CardBody,
  CustomInput,
  FormGroup,
  Modal,
  Row,
} from 'reactstrap';
import { useHistory } from 'react-router-dom';

import AddSponsorModel from '../../../components/application/AddSponsorModal';
import { Colxx, Separator } from '../../../components/common/CustomBootstrap';
import { adminRoot } from '../../../constants/defaultValues';

import Breadcrumb from '../../../containers/navs/Breadcrumb';
import AppError from '../../../helpers/AppError';
import IntlMessages from '../../../helpers/IntlMessages';
import SponsorsList from '../../../components/application/SponsorsList';

const Sponsors = ({ match }) => {
  const { id } = match.params;

  if (!id) throw new AppError('Please provide a Hackathon ID');

  const history = useHistory();

  const [openAddModal, setOpenAddModal] = useState(false);

  return (
    <>
      <Row>
        <Colxx xxs="12">
          <Breadcrumb heading="menu.sponsors" match={match} />
          <Separator className="mb-5" />
        </Colxx>
      </Row>

      <Row className="justify-content-center">
        <Colxx xxs="12" md="6">
          <SponsorsList id={id} />
        </Colxx>

        <Colxx xxs="12" md="6">
          <Card>
            <CardBody>
              <Row className="justify-content-center m-2">
                <Button onClick={() => setOpenAddModal(true)}>
                  <IntlMessages id="challenges.add-sponsor-button" />
                </Button>
              </Row>

              <Row className="justify-content-center m-2">
                <CustomInput
                  type="checkbox"
                  label={<IntlMessages id="challenges.add-sponsor-checkbox" />}
                />
              </Row>

              <Modal
                isOpen={openAddModal}
                toggle={() => setOpenAddModal(!openAddModal)}
              >
                <AddSponsorModel setOpenAddModal={setOpenAddModal} id={id} />
              </Modal>
            </CardBody>
          </Card>
        </Colxx>
      </Row>

      <Row>
        <Colxx>
          <FormGroup className="text-center">
            {/* <Button
              color="primary"
              className="m-3"
              onClick={() =>
                history.push(`${adminRoot}/challenges/judges/${id}`)
              }
            >
              <IntlMessages id="forms.previous" />
            </Button> */}
            <Button
              color="primary"
              className="m-3"
              onClick={() => history.push(`${adminRoot}/challenges/application/${id}`)}
            >
              <IntlMessages id="forms.next" />
            </Button>
          </FormGroup>
        </Colxx>
      </Row>
    </>
  );
};

export default Sponsors;
