import React from 'react';
import { /* Button, */ Card, CardBody, CardSubtitle } from 'reactstrap';

import { NavLink } from 'react-router-dom';

const SponsorCard = ({ data }) => {
  let isImg = false;
  const img = "/assets/img/profiles/l-2.jpg"
  if(data.logo.match(/\.(jpeg|jpg|gif|png)$/) !== null)
  {
    isImg = true;
  }
  return (
    <Card className="d-flex flex-row mb-4">
      <NavLink to={data.logo} className="d-flex">
        <img src={isImg?data.logo:img} alt="Not Found" width="100px" className="m-4" />
      </NavLink>
      <div className=" d-flex flex-grow-1 min-width-zero">
        <CardBody className=" pl-0 align-self-center d-flex flex-column flex-lg-row justify-content-between min-width-zero">
          <div className="min-width-zero">
            <NavLink to={data.logo}>
              <CardSubtitle className="truncate mb-1">{data.name}</CardSubtitle>
            </NavLink>
            {/* <Button outline size="xs" color="danger">
              Remove
            </Button> */}
          </div>
        </CardBody>
      </div>
    </Card>
  );
};

export default SponsorCard;
