/* eslint-disable react-hooks/exhaustive-deps */
import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import { Card, CardBody } from 'reactstrap';
import AppError from '../../helpers/AppError';
import IntlMessages from '../../helpers/IntlMessages';
import { getSponsors } from '../../redux/actions';
import SponsorCard from '../cards/SponsorCard';


const SponsorsList = ({
  id,
  accessToken,
  sponsors,
  loading,
  error,
  getSponsorsAction,
}) => {
  useEffect(() => {
    getSponsorsAction(accessToken, id);
  }, []);

  if (error) throw new AppError(error);

  return (
    <>
      <Card className="mb-4">
        <CardBody>
          <h3 className="h3">
            <IntlMessages id="challenges.added-sponsors" />
          </h3>
        </CardBody>
      </Card>

      {loading ? (
        <div className="d-flex justify-content-center m-4">
          <div className="loading" role="status">
            <span className="sr-only">Loading...</span>
          </div>
        </div>
      ) : (
        <>
          {sponsors.map((item) => (
            <SponsorCard key={item.id} data={item} />
          ))}
        </>
      )}
    </>
  );
};

const mapStateToProps = ({ authUser, applicationData }) => {
  const { accessToken } = authUser;
  const { sponsors, loading, error } = applicationData;
  return { accessToken, sponsors, loading, error };
};

export default connect(mapStateToProps, {
  getSponsorsAction: getSponsors,
})(SponsorsList);
